import { useAppState } from 'shared/state';
import { useLoadAll, usePagination } from '../../../shared/hooks';
import { useDispatch } from 'react-redux';
import Actions from '../../../store/actions';
const FormRequest = Actions.entity.formRequest;

export function useDepartmentList(
	type: string,
	searchQuery: string,
	departmentId?: number,
	isStaff?: string
): any {
	const { companyId } = useAppState();
	const { page } = usePagination(type === 'department' ? 'page' : 'subPage');
	const dispatch = useDispatch();
	const { reloadEntityAll } = useLoadAll();

	const handleDepOrderChange = (evt: any, id: number): any => {
		if (id) {
			dispatch(
				FormRequest({
					entity: 'sorder',
					name: 'AllSorder',
					url: `/${companyId}/departments/${id}/change_order/${evt.target.value}/`,
					params: {},
					method: 'put',
					values: {},
					cb: {
						success: () => {
							reloadEntityAll({
								entity: type === 'department' ? 'departments' : 'subDepartments',
								name:
									type === 'department'
										? `AllDepartments-${companyId}`
										: `SubDepartments-${companyId}`,
								url:
									type === 'department'
										? `/${companyId}/departments/`
										: `/${companyId}/departments/${departmentId}/sub_departments/`,
								params: {
									page: page,
									limit: 10,
									extra: {
										is_staff: isStaff ? isStaff : null,
										search: searchQuery
									}
								}
							});
						},
						error: () => {},
						finally: () => {}
					}
				})
			);
		}
	};

	return { handleDepOrderChange };
}
