import React, { useState } from 'react';
import { useAppState } from 'shared/state';
import { WhiteModal, Button, DialogActions, DialogTitle } from 'shared/components/Elements';
import { ErrorBoundary, Loader } from 'shared/components';
import { useEntityForm } from 'modules/entity/hooks/useEntityForm';
import { useNotistack, useQueryParams } from 'shared/hooks';
import { Trans } from 'react-i18next';

type Props = {
	handleModalOpen: (item: any | null, hasSiblings: boolean) => void;
	open: any | null;
};

function DepartmentDeleteModalInner({ handleModalOpen, open }: Props) {
	const { companyId } = useAppState();
	const [loading, setLoading] = useState(false);
	const { showNotification } = useNotistack();
	const { changeUrlParams, query } = useQueryParams<{ page: number }>();
	const page = query?.page as string | undefined;

	const { handleRequest: handleDeleteRequest } = useEntityForm({
		entity: 'departments',
		name: `AllDepartments-${companyId}`,
		method: 'delete',
		url: `/${companyId}/departments/${open?.entity?.id}/`,
		deleteData: true,
		id: open?.entity?.id,
		cb: {
			success: (data) => {
				handleModalOpen(null, false);
				setLoading(false);
				showNotification({ message: 'Successfully deleted', variant: 'success' });

				// forward to
				if (!open.hasSiblings && page && parseInt(page) > 1) {
					changeUrlParams({ page: parseInt(page) - 1 });
				}
			},
			error: (error) => {
				showNotification({
					message: error.response.data.message || 'Non empty department can not be deleted',
					variant: 'error'
				});
				setLoading(false);
				handleModalOpen(null, false);
			},
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			finally: () => {}
		}
	});

	const rejectRequest = (branch: any) => {
		setLoading(true);
		handleDeleteRequest();
	};

	const handleDelete = () => {
		if (open?.entity) rejectRequest(open);
	};

	const closeModal = () => {
		handleModalOpen(null, false);
	};

	return (
		<div className="relative">
			<WhiteModal handleModalOpen={closeModal} open={Boolean(open?.entity)}>
				<DialogTitle id="alert-dialog-title">
					<Trans i18nKey="delete_department_confirmation">
						Are you sure you want to delete the "<strong>{{ name: open?.entity?.name }}</strong>"
						department ?
					</Trans>
				</DialogTitle>
				<DialogActions>
					<Button onClick={closeModal} autoFocus variant="contained">
						<Trans i18nKey="cancel" />
					</Button>
					<Button onClick={handleDelete} variant="contained" color="secondary">
						<Trans i18nKey="delete" />
					</Button>
				</DialogActions>
				{loading && (
					<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
						<Loader />
					</div>
				)}
			</WhiteModal>
		</div>
	);
}

export function DepartmentDeleteModal(props: Props) {
	return (
		<ErrorBoundary>
			<DepartmentDeleteModalInner {...props} />
		</ErrorBoundary>
	);
}
