import React, { useCallback, useState } from 'react';
import {
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import { Pagination, TableBody, TableContainer, TableHead } from 'shared/components/Elements';
import { usePagination, useQueryParams, useWhiteModal } from 'shared/hooks';
import { useDepartmentContext } from '../context/DepartmentContext';
import { StaffDepartmentRequestTypes } from 'shared/typings/department.types';
import StaffDepartmentIcon from './staffTableDetails/StaffDepartmentIcon';
import { BsFillTelephoneFill } from 'react-icons/bs';
import { IconDepartmentMembers } from 'shared/icons/IconDepartmentMembers';
import { AiFillDelete, AiFillEye } from 'react-icons/ai';
import { MdModeEditOutline } from 'react-icons/md';
import IconButton from '@material-ui/core/IconButton';
import { MenuItem, Select } from '@material-ui/core';
import EditDepartmentForm from './EditDepartment';
import { Link } from 'react-router-dom';
import { DepartmentDeleteModal } from './DepartmentDeleteModal';
import { useDepartmentList } from '../hooks/useDepartmentList';
import { useDebounce } from 'use-debounce';
import qs from 'query-string';
import CopyEmailButton from './CopyEmailButton';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';

function StaffDepartmentRequest() {
	const { companyId, isAdmin, isPermitted } = useAppState();
	const { t } = useTranslation();
	const { isRTL } = useTranslationHelpers();
	const { page, handlePage } = usePagination();
	const { isStaff, searchQuery } = useDepartmentContext();
	const { WhiteModal, modalHandler: closeEditModal } = useWhiteModal();
	const [deleteModal, setDeleteModal] = useState<{
		hasSiblings: boolean;
		entity: StaffDepartmentRequestTypes | null;
	} | null>(null);
	const { handleDepOrderChange } = useDepartmentList('department', searchQuery, 0, isStaff);

	const { query } = useQueryParams();
	const searchString = qs.stringify(query);

	const [querySearch] = useDebounce<string>(searchQuery, 500);

	const [modifyModal, setModifyModal] = useState<StaffDepartmentRequestTypes | null>(null);

	const renderSelectItems = (count: number | undefined) => {
		return [...Array(count)].map((_, key) => {
			return (
				<MenuItem key={key} value={key + 1}>
					{Number(key + 1)}
				</MenuItem>
			);
		});
	};

	const handleModal = useCallback(
		(item: StaffDepartmentRequestTypes | null) => setModifyModal(item),
		[]
	);
	const handleDeleteModal = useCallback(
		(item: StaffDepartmentRequestTypes | null, hasSiblings: boolean) =>
			setDeleteModal({ hasSiblings, entity: item }),
		[]
	);

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity={'departments'}
					name={`AllDepartments-${companyId}`}
					url={`/${companyId}/departments/`}
					replaceIds
					params={{
						limit: 10,
						extra: {
							is_staff: isStaff,
							search: querySearch
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const departments = items.map((item: unknown) => item) as StaffDepartmentRequestTypes[];
						return (
							<div>
								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell
													style={{ width: '17%', textAlign: isRTL ? 'right' : 'left' }}
												>
													{t('department')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '17%', textAlign: 'center' }}>
													{t('phone_number')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '10%', textAlign: 'center' }}>
													{t('members')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '15%', textAlign: 'center' }}>
													<div className="truncate">{t('sub_department')}</div>
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '7%', textAlign: 'center' }}>
													{t('cases')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '5%' }}>
													{t('order')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													<div className="flex-centered">{t('email')}</div>
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '15%', textAlign: 'center' }}>
													{t('actions')}
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{departments.map((item: StaffDepartmentRequestTypes) => {
												const {
													id,
													icon,
													name,
													phone,
													members_count,
													department_case_count,
													sorder,
													acc_email_alias,
													sub_departments_count,
													acc_status,
													auto_case_create
												} = item;
												const mobile = phone ? (phone[0] === '+' ? phone : `+${phone}`) : null;
												return (
													<StyledTableRow key={id}>
														<StaffDepartmentIcon icon={icon} name={name} id={id} />
														<StyledTableCell>
															{mobile ? (
																<div className="flex items-center justify-center">
																	<BsFillTelephoneFill color="#979797" size="15px" />
																	<a
																		dir="ltr"
																		href={`tel:${mobile}`}
																		className="test-kdark-text mx-2 align-middle text-sm text-primary hover:text-primary-dark"
																	>
																		{mobile}
																	</a>
																</div>
															) : null}
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-center ">
																<IconDepartmentMembers />
																<p className="mx-2 text-sm font-semibold">{members_count}</p>
															</div>
														</StyledTableCell>
														<StyledTableCell>
															<div
																dir="ltr"
																className="flex items-center justify-center pr-4 text-center text-sm  font-semibold"
															>
																{sub_departments_count}
															</div>
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center pr-4 text-sm font-semibold">
																{department_case_count}
															</div>
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center px-2">
																<Select
																	disabled={!isAdmin}
																	className="text-sm font-semibold"
																	value={Number(sorder)}
																	onChange={(evt) => handleDepOrderChange(evt, item.id)}
																>
																	{renderSelectItems(meta.count)}
																</Select>
															</div>
														</StyledTableCell>
														<StyledTableCell>
															<div className=" flex-centered px-2">
																{acc_email_alias && auto_case_create ? (
																	<CopyEmailButton copiedEmail={acc_email_alias} />
																) : (
																	<>
																		{(() => {
																			if (!auto_case_create && acc_email_alias) {
																				return t('deactivated');
																			} else {
																				return (
																					<>
																						{acc_status ? (
																							<p>{t('not_ready_yet')}</p>
																						) : (
																							<div>{t('none')}</div>
																						)}
																					</>
																				);
																			}
																		})()}
																	</>
																)}
															</div>
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-center">
																<div className={clsx({ 'cursor-not-allowed': !isAdmin })}>
																	<IconButton
																		disabled={!isAdmin}
																		onClick={() => handleDeleteModal(item, departments.length > 1)}
																	>
																		<AiFillDelete color="#979797" size="18px" />
																	</IconButton>
																</div>

																<div className={clsx({ 'cursor-not-allowed': !isAdmin })}>
																	<IconButton
																		disabled={!isPermitted}
																		aria-label="view"
																		color="primary"
																	>
																		<Link to={`/departments/view/${id}/dep/?${searchString}`}>
																			<AiFillEye size="18px" />
																		</Link>
																	</IconButton>
																</div>

																<div className={clsx({ 'cursor-not-allowed': !isAdmin })}>
																	<IconButton
																		disabled={!isPermitted}
																		onClick={() => handleModal(item)}
																	>
																		<MdModeEditOutline color="#979797" size="18px" />
																	</IconButton>
																</div>
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
								<WhiteModal
									maxWidth={660}
									minWidth={660}
									handleModalOpen={closeEditModal}
									open={Boolean(modifyModal)}
									scrollable
									noPadding
								>
									<EditDepartmentForm handleModalOpen={handleModal} open={modifyModal} />
								</WhiteModal>

								<DepartmentDeleteModal handleModalOpen={handleDeleteModal} open={deleteModal} />

								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export default function DepartmentList() {
	return (
		<ErrorBoundary>
			<StaffDepartmentRequest />
		</ErrorBoundary>
	);
}
