import React from 'react';
import { getBusinessIcon } from 'shared/services/utils/getBusinessIcon';
import { StyledTableCell } from 'shared/components';
import { StaffDepartmentIconTypes } from 'shared/typings/department.types';
import { Link } from 'react-router-dom';
import { useQueryParams } from '../../../../shared/hooks';
import qs from 'query-string';

export type StaffDepartmentIconType = {
	icon: StaffDepartmentIconTypes;
	name: string;
	id: number;
};

export default function StaffDepartmentIcon({ icon, name, id }: StaffDepartmentIconType) {
	const { query } = useQueryParams();
	const searchString = qs.stringify(query);

	return (
		<StyledTableCell>
			<div className="flex items-center">
				<div className="h-9 w-9 flex-shrink-0">
					{getBusinessIcon(icon?.thumbnail || icon?.thumbnail_150 || '', name)}
				</div>
				<Link to={`/departments/view/${id}/dep/?${searchString}`}>
					<p className="mx-2.5 text-sm font-medium text-primary transition hover:text-primary-dark">
						{name}
					</p>
				</Link>
			</div>
		</StyledTableCell>
	);
}
